<template>
    <transition name="modal">
        <div class="modal" @click="closeModal">
            <form action="" class="modal__block" @submit="sendData" @click.stop="">
                <h2 class="modal__title">Отправить картинку</h2>
                <div class="modal__inputs">
                    <label>
                        <span>URL</span>
                        <input type="text" placeholder="URL" class="modal__input" v-model="url">
                    </label>
                    <label>
                        <span>Комментарий</span>
                        <input type="text" placeholder="Комментарий" class="modal__input" v-model="comment">
                    </label>
                </div>
                <div class="modal__btns">
                    <button class="modal__btn cancel" @click="closeModal">Отмена</button>
                    <button class="modal__btn sent" @click="sendData">ОТПРАВИТЬ</button>
                </div>
            </form>
        </div>
    </transition>
</template>

<script>

import { v4 as uuidv4 } from 'uuid';

export default {
    data() {
        return {
            url: '',
            comment: '',
            id: uuidv4(),
        }
    },
    props: {
        fileAdr: Object,
    },
    methods: {
        closeModal() {
            this.$emit('closeModal', false);
            this.url = '';
            this.comment = '';
        },
        sendData() {
            if (this.url != '' && this.checkUrl) {
                let item = {
                    id: this.id, date: String(new Date()), src: this.url, text: this.comment, from: this.fileAdr.from, to: this.fileAdr.to
                };
                this.$emit('addMes', item);
                this.url = '';
                this.comment = '';
                this.closeModal()
            };
        }
    },
    computed: {
        checkUrl() {
            let formats = ['png', 'jpg', 'webp', 'jpeg', 'gif', 'svg']
            for (let i = 0; i < formats.length; i++) {
                if (this.url.includes(formats[i])) {
                    return true;
                }
            }
            alert('Ссылка битая или уже не существует. \n(Принимаются только форматы: png, jpg, webp, jpeg, svg и gif)');
            this.url = '';
            this.comment = '';
            return false;
        }
    },
}
</script>

<style>
.modal-enter-active,
.modal-leave-active {
    transition: all 0.2s linear;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
    transform: scale(1.3);
}
</style>