<template>
    <div class="send" v-if="message.from == 'left'">
        <div class="send__messages" v-if="checkImg == false">
            <p class="send__desc">{{ message.text }}</p>
            <p class="send__date">{{ message['date'].slice(16, 21) }}</p>
        </div>
        <div class="send__files" v-else>
            <div class="send__files-block">
                <img :src="this.message.src" alt="ImgSend" class="send__img">
                <p class="send__desc" v-if="message.text != ''">{{ message.text }}</p>
            </div>
            <p class="send__date">{{ message['date'].slice(16, 21) }}</p>
        </div>
    </div>
    <div class="get" v-else>
        <div class="get__messages" v-if="checkImg == false">
            <p class="get__date">{{ message['date'].slice(16, 21) }}</p>
            <p class="get__desc">{{ message.text }}</p>
        </div>
        <div class="get__files" v-else>
            <p class="get__date">{{ message['date'].slice(16, 21) }}</p>
            <div class="get__files-block">
                <img :src="this.message.src" alt="ImgGet" class="get__img">
                <p class="get__desc" v-if="message.text != ''">{{ message.text }}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        message: Object,
    },
    computed: {
        checkImg() {
            return this.message.src ? true : false
        }
    },
}
</script>

