<template>
    <div class="layout">
        <Header :name="name" :status="status" :avaSrc="avaSrc" />
        <Main :messages="messages" />
        <Footer :messages="messages" :authorMes="authorMes" @addMes="addMes" @openModal="openModal"/>
    </div>
</template>

<script>

import Header from '@/components/Header.vue'
import Main from '@/components/Main.vue'
import Footer from '@/components/Footer.vue'

export default {
    data() {
        return {
            authorMes: 'left',
        }
    },
    components: {
        Header, Main, Footer
    },
    props: {
        name: String,
        status: String,
        avaSrc: String,
        messages: Array,
    },
    methods: {
        addMes(mesg) {
            this.$emit('addMes', mesg)
        },
        openModal(obj){
            this.$emit('openModal', obj)
        }
    },
}
</script>

<style></style>