<template>
  <div class="project">
    <LeftUser :name="name[0]" :status="status[0]" :avaSrc="avaSrc[0]" :messages="messages" @addMes="addMes"
      @openModal="openModal" />
    <Modal @addMes="addMes" @closeModal="closeModal" v-show="modal" :fileAdr="fileAdr" />
    <RightUser :name="name[1]" :status="status[0]" :avaSrc="avaSrc[1]" :messages="messages" @addMes="addMes"
      @openModal="openModal" />
  </div>
</template>

<script>
import LeftUser from '@/components/LeftUser.vue';
import RightUser from '@/components/RightUser.vue';
import Modal from '@/components/Modal.vue'

import leftpic from './assets/img/left-ava.png';
import rightpic from './assets/img/right-ava.png';


export default {
  components: {
    LeftUser, RightUser, leftpic, rightpic, Modal
  },
  data() {
    return {
      name: ['Александр', 'Евгений'],
      status: ['Онлайн', 'Офлайн'],
      avaSrc: [leftpic, rightpic],
      messages: [],
      modal: false,
      fileAdr: {}
    }
  },
  mounted() {
    localStorage.messages ? this.messages = JSON.parse(localStorage.messages) : localStorage.messages = JSON.stringify(this.messages);
    this.messages.sort((a, b) => a.date - b.date);
    localStorage.messages = JSON.stringify(this.messages);
  },
  methods: {
    addMes(mesg) {
      this.messages.push(mesg);
      this.refreshMessages();
    },
    refreshMessages() {
      this.messages.sort((a, b) => a.date - b.date);
      localStorage.messages = JSON.stringify(this.messages);
    },
    closeModal(boolen) {
      this.modal = boolen;
    },
    openModal(obj) {
      this.modal = obj.opened;
      this.fileAdr = { from: obj.from, to: obj.to };
    }
  },

}
</script>

