<template>
    <div class="layout">
        <Header :name="name" :status="status" :avaSrc="avaSrc" />
        <Main :messages="reversedMsg" />
        <Footer :messages="messages" :authorMes="authorMes" @addMes="addMes" @openModal="openModal"/>
    </div>
</template>

<script>

import Header from '@/components/Header.vue'
import Main from '@/components/Main.vue'
import Footer from '@/components/Footer.vue'

export default {
    data() {
        return {
            authorMes: 'right',
        }
    },
    components: {
        Header, Main, Footer
    },
    props: {
        name: String,
        status: String,
        avaSrc: String,
        messages: Array,
    },
    methods: {
        addMes(mesg) {
            this.$emit('addMes', mesg)
        },
        openModal(obj){
            this.$emit('openModal', obj)
        }
    },
    computed: {
        reversedMsg() {
            return this.messages.map(message => {
                return {
                    id: message.id,
                    date: message.date,
                    src: message.src,
                    text: message.text,
                    from: message.to,
                    to: message.from
                };
            });
        },
    }
}
</script>

<style></style>