<template>
    <footer class="footer">
        <form class="footer__form" @click.stop="" @submit.prevent="addMes">
            <input type="text" placeholder="Написать сообщение..." v-model="inputMsg">
            <button @click="addMes" v-if="inputMsg">
                <img src="@/assets/img/send.svg" alt="" class="submit__img">
            </button>
            <button v-else>
                <img src="@/assets/img/camera.svg" alt="" class="submit__img" @click="addPic">
            </button>
        </form>
    </footer>
</template>

<script>

import { v4 as uuidv4 } from 'uuid';

export default {
    data() {
        return {
            inputMsg: '',
            checkMsg: false,
            toAuthor: this.authorMes == 'right' ? 'left' : 'right',
            id: uuidv4(),
        }
    },
    props: {
        messages: Array,
        authorMes: String,
    },
    methods: {
        addMes() {
            if (this.inputMsg != '') {
                let item = {
                    id: this.id, date: String(new Date()), text: this.inputMsg, from: this.authorMes, to: this.toAuthor
                };
                this.$emit('addMes', item);
                this.inputMsg = '';
            }
        },
        addPic() {
            this.$emit('openModal', { opened: true, from: this.authorMes, to: this.toAuthor })
        }
    },
}
</script>

<style></style>